@import './apply-overlay.less';
@import './img-as-bg.less';
@import './card.less';
@import './ul_rvk-logos.less';
@import './forms.less';

.mx-auto {
  margin-left: auto;
  margin-right: auto;
}

hr {
  display: block;
  display: block;
  height: 2px;
  border: 0;
  margin: 2em 0;
  padding: 0;
  &.dashed {
    border-top: 1px dashed #cdcdcd;
  }
}


.row {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.block {
  display: block;
}

html.color-white {
  hr {
    &.dashed {
      border-color: @blackEye;
    }
  }
}

html.color-black {
  hr {
    &.dashed {
      border-color: @whiteEye;
    }
  }
}

.hey {
  border-radius: 50%;
}